import React from 'react';
import { View, Text, StyleSheet, TouchableOpacity, LogBox } from 'react-native';
import Icon from 'react-native-vector-icons/FontAwesome';
import { theme } from '../styling/theme';
import { LinearGradient } from 'expo-linear-gradient';
import OnlineStatus from './Header.OnlineStatus';
import { useNavigation } from '@react-navigation/native';
import { getNowTime } from '../utils/commonUtil';
import { useDispatch } from 'react-redux';
import { setDialogMessages, setInstructionTexts } from '../redux/infoSlice';
import { setFloorArray, setMapFloor } from '../redux/mapSlice';

// ignore this warning and pass functions thought route params safely.
LogBox.ignoreLogs([
	'Non-serializable values were found in the navigation state',
]);

const ChatHeader = ({ botname, locationName, onlineStatus, sendJsonMessage}) => {
	// use the useNavigation to provide the navigation prop automatically
	const navigation = useNavigation();

	// redux
	const dispatch = useDispatch();

	const handleReset = () => {
		const [hour, min] = getNowTime();
        sendJsonMessage({
            'author': "user",
			'type':'text',
			'message': '/done',
			'time': hour + ':' + min,
        });
        dispatch(setDialogMessages([]));
		dispatch(setInstructionTexts({}));
        dispatch(setMapFloor(-100));
        dispatch(setFloorArray([]));
	}

    return (
		<LinearGradient colors={['#C33764', '#1D2671', '#192f6a']} style={styles.container}>
			<TouchableOpacity style={styles.backButton}>
				<Icon name="angle-left" size={30} color={theme.colors.white} />
			</TouchableOpacity>
			<View style={styles.profileOptions}>
				<TouchableOpacity style={styles.profile}>
					<View style={styles.usernameAndLocationName}>
						<View style={styles.usernameAndOnlineStatus}>
							<Text style={styles.username}>{botname}</Text>
							<OnlineStatus onlineStatus={onlineStatus}/>
						</View>
						<View>
							<Text style={styles.locationName}>{locationName}</Text>
						</View>
					</View>
				</TouchableOpacity>
				<View style={styles.buttonsViewStyle}>
					<TouchableOpacity style={styles.options} onPress={() => handleReset()}>
						<Text style={styles.mapButton}>重新對話</Text>
					</TouchableOpacity>
					<TouchableOpacity style={styles.options} onPress={() => navigation.navigate("MapNavigation", {
						sendJsonMessage
					})}>
						<Text style={styles.mapButton}>地圖模式</Text>
					</TouchableOpacity>
				</View>
			</View>
		</LinearGradient>
    )
}

const styles = StyleSheet.create({
	container: {
		flexDirection: "row",
		backgroundColor: theme.colors.primary,
		paddingTop: 25,
		paddingBottom: 10,
	},
	backButton: {
		alignSelf: "center",
		paddingHorizontal: 10,
	},
	profileOptions: {
		flex: 1,
		flexDirection: "row",
		justifyContent: "space-between",
		alignItems: "center",
		paddingHorizontal: 10,
	},
	profile: {
		flexDirection: "row",
		alignItems: "center",
		borderColor: "#fff",
		flex: 4,
	},
	image: {
		height: 65,
		width: 65,
		borderRadius: 32.5,
	}
	,
	usernameAndOnlineStatus: {
		flexDirection: "row",
		justifyContent: "center",
		paddingHorizontal: 10,
	},
	usernameAndLocationName: {
		flexDirection: "column",
		paddingHorizontal: 10,
	},
	username: {
		color: theme.colors.white,
		fontSize: 18,
		fontWeight: "bold",
	},
	locationName: {
		color: theme.colors.white,
		fontWeight: "bold",
		fontSize: 16,
		paddingHorizontal: 10,
	},
	buttonsViewStyle: {
		flexDirection: 'row',
	},
	options: {
		paddingHorizontal: 6, 
		paddingVertical: 10, 
		borderRadius: 5, 
		backgroundColor: "#68a0cf",
		marginHorizontal: 5,
	},
	mapButton: {
		color: "white"
	}
})

export default ChatHeader