import React, { useState, useRef, useEffect } from "react";
import { Platform, ScrollView, KeyboardAvoidingView } from "react-native";
// import Tts from 'react-native-tts';
import { theme } from "../styling/theme";
import Message from "./Message.Main";
import { GestureHandlerRootView } from "react-native-gesture-handler";
import { useDispatch, useSelector } from "react-redux";
import { selectDialogMessages, selectInstructions, setIndex } from "../redux/infoSlice";
import { selectFloorIndex, selectMapFloor, setFloorArray, setFloorIndex, setMapFloor } from "../redux/mapSlice";



const ChatMessagessList = ({ sendJsonMessage }) => {
	const user = useRef("user");
	const scrollView = useRef();
	const [scrollViewEnable, setScrollViewEnable] = useState(true);

	const dispatch = useDispatch();
	const dialogMessages = useSelector(selectDialogMessages);
	const instructions = useSelector(selectInstructions);
	const mapFloor = useSelector(selectMapFloor);
	const floorIndex = useSelector(selectFloorIndex);
	useEffect(() => {
		let newFloorArray = [];
		for (var i=0, len=dialogMessages.length; i<len; ++i) {
			let currentMessage = dialogMessages[i];
			if (currentMessage['type'] === 'image') {
				let floorNumber = Number(currentMessage['floor']);
				newFloorArray.push(floorNumber);
				if (mapFloor == -100) {
					dispatch(setMapFloor(floorNumber));
					dispatch(setIndex(0));
				}
			}
		}
		if (newFloorArray.length > 0 && floorIndex == -1) {
			// 如果 FloorArray 開始有值，floorIndex 還在未定義的狀態: 表示剛開始要導航，這時強制設定floor index = 0
			dispatch(setFloorIndex(0));
		}
		dispatch(setFloorArray(newFloorArray)); 
    }, [instructions])


	return (
		<ScrollView style={{ backgroundColor: theme.colors.white, flex: 1 }}
			ref={scrollView}
			onContentSizeChange={() => {
				scrollView.current.scrollToEnd({ animated: true })
			}}
			scrollEnabled={scrollViewEnable}
			// onTouchEnd={() => { setScrollViewEnable(true) }}
			// onTouchStart={(event) => {
			// 	if (event.nativeEvent.locationY < 600) setScrollViewEnable(false);

			// }}
		>
			<KeyboardAvoidingView
                behavior={Platform && Platform.OS === "ios" ? "padding" : "height"}
                style={{flex:1}}
              >
				<GestureHandlerRootView>
				{dialogMessages.map((message, index) => 
					(message.author != undefined) ?
						<Message
							key={index}
							time={message.time}
							isLeft={message.author !== user.current}
							message={message.message}
							messageType={message.type}
							floor={message.floor}
							index = {message.index}
							optionRefDec = {message.optionRefDec}
							setScrollState = {setScrollViewEnable}
							sendJsonMessage={sendJsonMessage}
						/> : null
				)}
				</GestureHandlerRootView>
			</KeyboardAvoidingView>

		</ScrollView>
	);
};

export default ChatMessagessList;