import { useState, useEffect, useRef } from 'react';
import { StyleSheet, View, Button, ImageBackground, Text, TouchableOpacity, Platform, SafeAreaView } from 'react-native';
import MaterialCommunityIcons from 'react-native-vector-icons/MaterialCommunityIcons';
import ReactNativeZoomableView from '@openspacelabs/react-native-zoomable-view/src/ReactNativeZoomableView';
import Svg ,{ Defs, Marker, Path, Circle } from 'react-native-svg';
import { useSelector, useDispatch } from 'react-redux';
import Modal from "react-native-modal";
import { selectInstructions, selectInstructionTexts, selectIndex, setIndex, setDialogMessages, setInstructionTexts } from '../redux/infoSlice';
import { selectMapFloor, selectFloorArray, selectFloorIndex, setFloorIndex, setMapFloor, setFloorArray } from '../redux/mapSlice';
import { 
    calcAngleDegrees, 
    changePath, 
    computeVector, 
    getNowTime, 
    getPathCoordinates, 
    DOMAIN_NAME, 
    HTTP_SCHEME, 
    PORT_django, 
    rotatePoint, 
    rotateRelativeToCenter 
} from '../utils/commonUtil';


const MapNavigationScreen = ({route, navigation}) => {

    const { sendJsonMessage } = route.params;

    // redux
    const dispatch = useDispatch();
    const instructions = useSelector(selectInstructions);
    const mapFloor = useSelector(selectMapFloor);

    const instructiontext = useSelector(selectInstructionTexts);
    const infoIndex = useSelector(selectIndex);
    const floorArray = useSelector(selectFloorArray);
    const floorIndex = useSelector(selectFloorIndex);

    const getNextStepIndices = (shift, curFloorIndex, curInfoIndex) => {
        if (shift < 0) {
            if (curInfoIndex == 0 && curFloorIndex == 0) {
                return {
                    flag: false,
                    result: []
                };
            }

            if (curInfoIndex != 0) {
                return {
                    flag: true,
                    result: [
                        {index: curInfoIndex - 1}
                    ]
                }
            }
            else {
                let instructArray = instructiontext[floorArray[curFloorIndex-1]]["subCategory"];
                return {
                    flag: true,
                    result: [
                        {floor: curFloorIndex - 1},
                        {index: instructArray.length - 1}
                    ]
                };
            }
        }
        else {
            let instructArray = instructiontext[floorArray[curFloorIndex]]["subCategory"];
            if (curFloorIndex == floorArray.length - 1 && curInfoIndex == instructArray.length - 1) {
                return {
                    flag: false,
                    result: []
                };
            }

            if (curInfoIndex < instructArray.length - 1) {
                return {
                    flag: true,
                    result:[
                        {index: curInfoIndex + 1}
                    ]
                };
            }
            else {
                return {
                    flag: true,
                    result: [
                        {index: 0},
                        {floor: curFloorIndex + 1}
                    ]
                };
            }
        }
    };

    const ifGuideStart = () => {
        // console.log('instructiontext !== {}: ,', instructiontext !== {});
        // console.log('floorArray != []: ,', floorArray != []);

        return floorArray.length > 0 && infoIndex !== -1;
    };

    const getFutureInstructionText = (currentFloorIndex, currentInfoIndex) => {
        if (!ifGuideStart()) {
            return "";
        }

        const future = getNextStepIndices(1, currentFloorIndex, currentInfoIndex);
        if (!future.flag) {
            return "(已完成)";
        }
        else {
            let notyet_floorIndex = currentFloorIndex;
            let notyet_index = currentInfoIndex;
            future.result.forEach((obj) => {
                if (obj.index !== undefined) {
                    notyet_index = obj.index;
                }
                else {
                    notyet_floorIndex = obj.floor;
                }
            });
            return instructiontext[floorArray[notyet_floorIndex]]["subCategory"][notyet_index]["name"];
        }
    };

    const [nextInstructionText, setNextInstructionText] = useState(getFutureInstructionText(floorIndex, infoIndex));

    const goNextStep = (shift) => {

        const res = getNextStepIndices(shift, floorIndex, infoIndex);
        if (!res.flag) {
            if (shift > 0) {
                // 詢問是否結束導航
                setModalContent("你已完成導航路徑，確定要結束嗎?");
                setIsModalVisible(true);

            }
            return;
        }
        else {
            let newFloorIndex = floorIndex;
            let newIndex = infoIndex;

            res.result.forEach((obj) => {
                if (obj.index !== undefined) {
                    newIndex = obj.index;
                    dispatch(setIndex(obj.index));
                }
                else {
                    newFloorIndex = obj.floor;
                    dispatch(setFloorIndex(obj.floor));
                    dispatch(setMapFloor(floorArray[obj.floor]));
                }
            });

            setNextInstructionText(getFutureInstructionText(newFloorIndex, newIndex));
        }
    };

    // svg
    const [imageURL, setImageURL] = useState(`${HTTP_SCHEME}://${DOMAIN_NAME}:${PORT_django}/static/image/03.jpeg`)

    const img_width = 1024;
    const img_height = 557;

    const ratio_img_to_svg = 2.5;
    var svg_width = img_width / ratio_img_to_svg;   // 409.6
    var svg_height = img_height / ratio_img_to_svg; // 222.8
    var viewBoxProps = "0 0 1024 557"; // equal to original image's size

    const [svgStepPathNow, setSvgStepPathNow] = useState('M 0 0 L 0 0');
    const [svgPath, setSvgPath] = useState('M 0 0 L 0 0');
    const [svgDonePath, setSvgDonePath] = useState('M 0 0 L 0 0');

    // ZoomableView centering
    const zoomableViewRef = useRef();
    const initialZoomValue = 4;
    let zoomVal = useRef(initialZoomValue);
    // coordinate of imageCenter, when image center coincides with 畫面 center
    const imageCenter = { x: 0.5*img_width, y: 0.5*img_height };
    const initialBasePoint = {x: 0.5*img_width, y: 0.5*img_height + 160*ratio_img_to_svg/initialZoomValue};
    let basePoint = useRef(initialBasePoint);
    const [angle, setAngle] = useState(0);

    const scale_to_canvas = (vector) => {
        return [initialZoomValue*(vector.x/ratio_img_to_svg), initialZoomValue*(vector.y/ratio_img_to_svg)];
    };

    const distance_center_target = (goal) => {

        const deltaVector = computeVector(imageCenter, goal);
        return scale_to_canvas(deltaVector);
    };

    let targetPoint = {x: 0, y: 0};
    let [previousMove, setPreviousMove] = useState({x:0, y:0});
    // for base point guide
    let previousAutoTotalMove = useRef({x: 0, y: 0});
    let previousTransformTotalMove = useRef({x: 0, y: 0});

    const updateBasePoint = (rotation_angle) => {
        const delta_rotation_angle = rotation_angle - angle;
        //console.log(`basePoint 原先為 (${basePoint.current.x}, ${basePoint.current.y})`);

        let newPoint = rotateRelativeToCenter(basePoint.current, imageCenter, -delta_rotation_angle);
        basePoint.current = {x: newPoint.x, y: newPoint.y};

        //console.log(`basePoint 更新為 (${basePoint.current.x}, ${basePoint.current.y})`);
    };

    /** (依圖片中心)順時針轉 ? 度 */ 
    const doRotation = (rotation_angle) => {
        // 原本轉角 = angle，現在要更新為 rotation_angle
        setAngle(rotation_angle);

        // update basePoint coordinate
        updateBasePoint(rotation_angle);
    };

    const rotateLineToVertical = (point1, point2) => {
        let d = calcAngleDegrees(point2.x-point1.x, point2.y-point1.y);
        let needToRotate = -(d + 90); // 順時針轉 ? relative to unrotate state

        //console.log('箭頭角度 = ', d, '度, 所以，新的順時鐘轉角 = ', needToRotate, '度');
        doRotation(needToRotate);

        return needToRotate;
    };

    const zoomCenteringGuide = (pathPoints) => {
        targetPoint = pathPoints[infoIndex];
        // console.log("new point coordinate: ", targetPoint.x, targetPoint.y);
        // console.log("previous_move: ", previousMove.x, previousMove.y);

        // before move, move to original state first!
        zoomableViewRef.current.moveBy(-previousMove.x, -previousMove.y);
        const [disX, disY] = distance_center_target(targetPoint);
        zoomableViewRef.current.moveBy(disX, disY);
        setPreviousMove({x: disX, y: disY});
    };

    const calibraToOriginalTargetPoint = () => {
        basePointGuide(undefined, true);
    };

    const basePointGuide_Going = (nowPathPoints) => {

        let updatedAngle = rotateLineToVertical(nowPathPoints[infoIndex], nowPathPoints[infoIndex+1]);
        let disVector = computeVector(basePoint.current, nowPathPoints[infoIndex]);
        let rotDicVector = rotatePoint(disVector, updatedAngle);
        const [disX, disY] = scale_to_canvas(rotDicVector);
        zoomableViewRef.current.moveBy(disX, disY);

        // consequence of auto move:
        targetPoint = nowPathPoints[infoIndex];
        basePoint.current = {x: targetPoint.x, y: targetPoint.y};
        previousAutoTotalMove.current = {x: previousAutoTotalMove.current.x + disX, y: previousAutoTotalMove.current.y + disY};
        // 同步: Transform move(by Hand) = Auto Move 等於沒有手移動了
        previousTransformTotalMove.current = previousAutoTotalMove.current;
    };

    const basePointGuide = (nowPathPoints, calibraOnly) => {

        if (calibraOnly === false) {
            if (infoIndex == nowPathPoints.length -1) {
                console.log("到最後一點了");
                return;
            }
        }

        // 先旋轉，再平移 image。 (若一開始位置偏移，就要先校正)
        if (zoomVal.current !== initialZoomValue) {
            // zoomTo() need some time to excute
            zoomableViewRef.current.zoomTo(initialZoomValue);

            setTimeout(() => {
                const calibraVector = {
                    x: -(previousTransformTotalMove.current.x - previousAutoTotalMove.current.x), 
                    y: -(previousTransformTotalMove.current.y - previousAutoTotalMove.current.y)
                };
                zoomableViewRef.current.moveBy(calibraVector.x, calibraVector.y);
                zoomVal.current = initialZoomValue;

                // Hand move = Auto Move 等於沒有手移動了
                previousTransformTotalMove.current = previousAutoTotalMove.current;

                if (calibraOnly === false) {
                    basePointGuide_Going(nowPathPoints);
                }
            }, 1200);
        }
        else if (previousTransformTotalMove.current !== previousAutoTotalMove.current) {
            const calibraVector = {
                x: -(previousTransformTotalMove.current.x - previousAutoTotalMove.current.x), 
                y: -(previousTransformTotalMove.current.y - previousAutoTotalMove.current.y)
            };
            // 校正時，不須紀錄autoMove
            zoomableViewRef.current.moveBy(calibraVector.x, calibraVector.y);
    
            // 同步: Transform move(by Hand) = Auto Move 等於沒有手移動了
            previousTransformTotalMove.current = previousAutoTotalMove.current;

            if (calibraOnly === false) {
                basePointGuide_Going(nowPathPoints);
            }
        }
        else {
            if (calibraOnly === false) {
                basePointGuide_Going(nowPathPoints);
            }
        }
    };
    
    useEffect(() => {
        if (mapFloor > -100 && infoIndex >= 0) {
            let tmpInstructions = instructions[mapFloor.toString()];
            let currentPathPoints = getPathCoordinates(tmpInstructions);

            // 將該樓層完整的導航path 拆成3段: 已走過/正在走/其他未來的
            let doneStepsInstructions = [];
            let nowStepInstructions = [];
            let futureStepsInstructions = [];
            if (infoIndex + 2 <= tmpInstructions.length) {
                nowStepInstructions = tmpInstructions.slice(infoIndex, infoIndex + 2);
            }
            else {
                nowStepInstructions = tmpInstructions.slice(infoIndex, infoIndex + 1);
            }

            if (infoIndex + 1 < tmpInstructions.length) {
                futureStepsInstructions = tmpInstructions.slice(infoIndex + 1);
            }

            doneStepsInstructions = tmpInstructions.slice(0, infoIndex + 1);

            setSvgDonePath(changePath(doneStepsInstructions));
            setSvgStepPathNow(changePath(nowStepInstructions));
            setSvgPath(changePath(futureStepsInstructions));
            
            setImageURL(`${HTTP_SCHEME}://${DOMAIN_NAME}:${PORT_django}/static/image/0${mapFloor}.jpeg`);


            // display strategy 1
            //zoomCenteringGuide(currentPathPoints);

            // display strategy 2
            basePointGuide(currentPathPoints, false);
        }
        else {
            // default image before indoor guide
            setSvgPath('M 0 0 L 0 0');
            setImageURL(`${HTTP_SCHEME}://${DOMAIN_NAME}:${PORT_django}/static/image/iFG-cover.jpg`);
        }
    }, [mapFloor, infoIndex]);

    // modal: use for check
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [modalContent, setModalContent] = useState("你已完成導航路徑，確定要結束嗎?");
    const handleModalCancel = () => {
        setIsModalVisible(false);
    };
    const handleModal2 = () => {
        setModalContent("你還未到達目的地，確定要結束嗎?");
        setIsModalVisible(true);
    };
    const handleFinish = () => {
        setIsModalVisible(false);

        navigation.navigate('ChatBot');

        const [hour, min] = getNowTime();
        sendJsonMessage({
            'author': "user",
			'type':'text',
			'message': '/done',
			'time': hour + ':' + min,
        });
        dispatch(setDialogMessages([]));
		dispatch(setInstructionTexts({}));
        dispatch(setMapFloor(-100));
        dispatch(setFloorArray([]));
    };

    return (
        <SafeAreaView style={styles.crossPlatformContainer}>

            <View style={styles.headerContainer}>
                <Button style={styles.goBackView} title="回到對話模式" onPress={() => navigation.goBack()} />
                <TouchableOpacity style={styles.calibrationButtonViewStyle} onPress={() => calibraToOriginalTargetPoint()}>
                    <Text style={styles.calibrationButtonTextStyle}>
                        定位回導航點
                        <MaterialCommunityIcons name="crosshairs-gps" color="black" size={25} />
                    </Text>
                </TouchableOpacity>
                <TouchableOpacity style={styles.stopButtonViewStyle} onPress={() => handleModal2()}>
                    <Text style={styles.calibrationButtonTextStyle}>
                        結束
                    </Text>
                </TouchableOpacity>
            </View>

            <View style={styles.bodyContainer}>
                <ReactNativeZoomableView
                    maxZoom={10} 
                    minZoom={1} 
                    zoomStep={0.5} 
                    initialZoom={initialZoomValue} 
                    bindToBorders={false} 
                    //onLongPress = {(event, gestureState) => console.log(gestureState)}
                    
                    // 觀念: image 在任何位置，旋轉角度均不會改變 zoomableViewEventObject的offsetX、offsetY
                    // onShiftingEnd = {(event, gestureState, zoomableViewEventObject) => {
                    //     // console.log(zoomableViewEventObject);
                    //     previousHandTotalMove.current = {x: -zoomableViewEventObject.offsetX*zoomVal.current, y: -zoomableViewEventObject.offsetY*zoomVal.current};
                    // }}
                    // onZoomEnd = {(event, gestureState, zoomableViewEventObject) => {
                    //     zoomVal.current = zoomableViewEventObject.zoomLevel;
                    // }}
                    onTransform = {(zoomableViewEventObject) => {
                        // two possibility: by guesture or by program
                        // console.log('onTransform called', zoomableViewEventObject);
                        zoomVal.current = zoomableViewEventObject.zoomLevel;
                        previousTransformTotalMove.current = {x: -zoomableViewEventObject.offsetX*zoomableViewEventObject.zoomLevel, y: -zoomableViewEventObject.offsetY*zoomableViewEventObject.zoomLevel}
                    }}
                    ref={zoomableViewRef}>

                    <ImageBackground
                        source={{ uri: imageURL }} 
                        style={[
                            styles.bgImageStyle, 
                            {
                                width: svg_width, 
                                height: svg_height, 
                                transform: [{ rotate: `${angle}deg` }, {translateX: 0}]
                            }
                        ]}
                    >

                        <Svg width={svg_width} height={svg_height} viewBox={viewBoxProps}>
                            <Defs>
                                <Marker
                                    id="MapModeTriangle"
                                    viewBox="0 0 10 10"
                                    refX="0"
                                    refY="5"
                                    markerUnits="strokeWidth"
                                    markerWidth="3"
                                    markerHeight="3"
                                    orient="auto"
                                >
                                    <Path
                                        d="M 0 0 L 10 5 L 0 10 z" 
                                        fill="black"
                                        stroke="red"
                                        strokeWidth="1"
                                    />
                                </Marker>
                                <Marker
                                    id="MapModestartPoint"
                                    viewBox="0 0 10 10"
                                    refX="5"
                                    refY="5"
                                    markerUnits="strokeWidth"
                                    markerWidth="3"
                                    markerHeight="3"
                                    orient="auto"
                                >
                                        <Circle 
                                            cx="5" 
                                            cy="5" 
                                            r="8" 
                                            fill="green"
                                        />
                                </Marker>
                                <Marker
                                    id="MapModemidPoint"
                                    viewBox="0 0 10 10"
                                    refX="5"
                                    refY="5"
                                    markerUnits="strokeWidth"
                                    markerWidth="2"
                                    markerHeight="2"
                                    orient="auto"
                                >
                                    <Circle cx="5" cy="5" r="8" fill="grey"/>
                                </Marker>
                            </Defs>

                            <Path
                                d={svgDonePath}
                                // d="M 0 0 L 10 5 L 0 10 z"
                                fill="none"
                                stroke="#333538"  // 灰色
                                strokeWidth="5"
                                strokeDasharray={"8,8"}
                                markerStart="url(#MapModemidPoint)"
                                markerMid="url(#MapModemidPoint)"
                                markerEnd="url(#MapModemidPoint)"
                                
                            />

                            <Path
                                d={svgStepPathNow}
                                // d="M 0 0 L 10 5 L 0 10 z"
                                fill="none"
                                stroke="#ff3333"  // 紅色
                                strokeWidth="5"
                                markerStart="url(#MapModestartPoint)"
                                markerMid="url(#MapModemidPoint)"
                                markerEnd="url(#MapModemidPoint)"
                                
                            />

                            <Path
                                d={svgPath}
                                // d="M 0 0 L 10 5 L 0 10 z"
                                fill="none"
                                stroke="#7a5252" // 棕色
                                strokeWidth="5"
                                markerStart="url(#MapModemidPoint)"
                                markerMid="url(#MapModemidPoint)"
                                markerEnd="url(#MapModeTriangle)"
                            />
                        </Svg>

                    </ImageBackground>

                </ReactNativeZoomableView>
            </View>
            
            {
                ifGuideStart() ?
                <View style={styles.bottomContainer}>
                    <View style={styles.guideContainer}>
                        <Text style={styles.mainTextStyle}>
                            {instructiontext[floorArray[floorIndex]]["subCategory"][infoIndex]["name"]}
                        </Text>
                    </View>

                    <View style={[styles.multiColumnContaner ]}>
                        <View style={styles.subKeywordViewContainer}>
                            <Text style={styles.subKeywordStyle}>接下來</Text>
                        </View>
                        <View style={styles.subTextViewContainer}>
                            <Text style={styles.subTextStyle}>{nextInstructionText}</Text>
                        </View>
                    </View>

                    <View style={styles.multiColumnContaner}>
                        <TouchableOpacity style={[styles.paginationButtonContainer, {borderRightColor: "white", borderRightWidth: 0.5}]} onPress={() => goNextStep(-1)}>
                            <Text style={styles.buttonTextStyle}>上一步</Text>
                        </TouchableOpacity>
                        <TouchableOpacity style={[styles.paginationButtonContainer, {borderLeftColor: "white", borderLeftWidth: 0.5}]} onPress={() => goNextStep(1)}>
                            <Text style={styles.buttonTextStyle}>下一步</Text>
                        </TouchableOpacity>

                    </View>
                </View>
                : <View style={styles.bottomContainer}></View>
            }


            <Modal isVisible={isModalVisible}>
                <View style={styles.modalViewContainer}>
                    <Text style={styles.modalTextStyle}>{modalContent}</Text>
                    <View style={{flexDirection: 'row', justifyContent: 'flex-end'}}>
                        <TouchableOpacity onPress={handleFinish}>
                            <Text style={styles.modalYesButtonStyle}>是</Text>
                        </TouchableOpacity>
                        <TouchableOpacity onPress={handleModalCancel}>
                            <Text style={styles.modalNoButtonStyle}>否</Text>
                        </TouchableOpacity>
                    </View>
                </View>
            </Modal>
        </SafeAreaView>

        
    );
}

const styles = StyleSheet.create({
    crossPlatformContainer: {
        flex: 1,
        paddingTop: Platform.OS === 'android' ? 0 : 0
    },
    headerContainer: {
        alignSelf: 'stretch',
        flexDirection: "row",
        justifyContent: "space-between"
    },
    bodyContainer: {
        flexGrow: 1,
        backgroundColor: "#EAF0F2"
    },
    bottomContainer: {
        marginHorizontal: 0,
        backgroundColor: "red"
    },
    goBackBotton: {
        flexGrow: 1
    },
    calibrationButtonViewStyle: {
        backgroundColor: "white",
        flexGrow: 1
    },
    calibrationButtonTextStyle:{
        color: "black",
        textAlign: "center",
    },
    stopButtonViewStyle: {
        backgroundColor: "#e6658c",
        flexGrow: 1,
        justifyContent: 'center'
    },
    bgImageStyle: {
		alignSelf: "center",
	},

    guideContainer: {
        backgroundColor: '#198039',
        paddingVertical: 12,
        paddingHorizontal: 15,
    },
    mainTextStyle: {
        color: "white",
        fontSize: 18,
        fontWeight: "500",
    },
    multiColumnContaner: {
        flexDirection:'row',
        justifyContent: "space-between",
    },
    subKeywordViewContainer: {
        width: 86,
        backgroundColor: "#eff2d7",
        paddingVertical: 10,
        paddingHorizontal: 15,
        justifyContent: 'center',
    },
    subKeywordStyle: {
        textAlign: "center",
        fontSize: 16,
    },
    subTextViewContainer: {
        justifyContent: 'center',
        paddingHorizontal: 10,
        backgroundColor: "#d7f3da",
        flexGrow: 1,
        flexShrink: 1,
    },
    subTextStyle: {
        fontSize: 15,
        textAlign: "center",
    },
    paginationButtonContainer: {
        flexGrow: 1,
        backgroundColor: "#0d662e",
    },
    buttonTextStyle: {
        paddingVertical: 10,
        textAlign: "center",
        fontSize: 14,
        color: "white",
        fontWeight: "500",
    },

    modalViewContainer: {
        justifyContent: "space-between",

        backgroundColor: "white", 
        height: 120, 
        borderRadius: 10,
        padding: 15
    },
    modalTextStyle: {
        fontSize: 20,
        textAlign: "center",
    },
    modalYesButtonStyle: {
        color: "#4584fc",
        paddingHorizontal: 18,
        fontSize: 16,
    },
    modalNoButtonStyle: {
        color: "#858585",
        paddingHorizontal: 18,
        fontSize: 16,
    },
})


export default MapNavigationScreen;