import { Pressable, StyleSheet, Text, View } from "react-native";
import Icon from "react-native-vector-icons/FontAwesome";
import { theme } from "../styling/theme";

const ModalWebSpeech = ({webSpeechMiddleResult, stopRecording}) => {
    return (
        <View style={styles.modalContainer}>
            <View style={styles.modalHeader}>
                <Icon name="microphone" size={32} color={theme.colors.danger}></Icon>
                <Text style={styles.modalPromptTextStyle}>辨識中，請說話</Text>
                <Pressable style={styles.modalButtonStyle} onPress={() => stopRecording()}>
                    <Text>關閉</Text>
                </Pressable>
            </View>
            <View>
                <Text style={styles.modalResultTextStyle}>{webSpeechMiddleResult}</Text>
            </View>
        </View>
    );
}

const styles = StyleSheet.create({
	modalContainer: {
		backgroundColor: 'white',
		paddingHorizontal: 20,
		paddingVertical: 20,
	},
	modalHeader: {
		flexDirection: 'row',
		alignItems: 'center',
		marginBottom: 12,
	},
	modalPromptTextStyle: {
		marginLeft: 10,
		marginRight: 50,
	},
	modalButtonStyle: {
		borderColor: '#dddddf',
		borderWidth: 2,
		borderRadius: 8,
		paddingVertical: 10,
		paddingHorizontal: 15
	},
	modalResultTextStyle: {
		color: '#2596be',
		fontSize: 18,
	}
});
 
export default ModalWebSpeech;