import React, {  useState } from "react";
import { View, Text, StyleSheet, TouchableOpacity,Dimensions } from "react-native";

import Animated, {
	useAnimatedStyle,
	useSharedValue,
} from "react-native-reanimated";
import { theme } from "../styling/theme";
import Icon from "react-native-vector-icons/FontAwesome";
import InnerBox from "./Message.InnerBox"

const ExpandableListView = ({ messageContent, isOnLeft, messageType, time, isLeft, floor}) => {
    const [isopen, setIsOpen] = useState(false)
    const x = useSharedValue(0);

    var innerBox = messageContent[0].subCategory
    
    const uas = useAnimatedStyle(() => {
		return {
			transform: [{ translateX: x.value }]
		}
	});
    const SCREEN_WIDTH = Dimensions.get('window').width;
	const TRANSLATE_X_THRESHOLD = - SCREEN_WIDTH *0.2;


    return (
        <View>
            <TouchableOpacity onPress={()=> setIsOpen(prev => !prev)}>
                <View style={[styles.container]}>
                    <Animated.View style={uas}>
                        <View
							style={[
								styles.messageContainer,
								isOnLeft("messageContainer"),
							]}
                        >
							<Text style={styles.HeadText}>{messageContent[0].categoryName + "之指示 "}    
								<Icon
									name= {isopen ?  "arrow-up" : "arrow-down"}
									color={theme.colors.description}
									style={[{left:30}]}
								/>
							</Text>
                        </View>
                    </Animated.View>
                </View>
            </TouchableOpacity>


			{isopen && innerBox.map((sub,index)=>(
			<InnerBox
				key={index}
				index={sub.index}
				message={sub.name}
				isOnLeft={isOnLeft} 
				floor={floor}
				messageType={messageType}
				time={time}
				isLeft={isLeft}
			/>

			))}

        </View>
    )

};

const styles = StyleSheet.create({
	container: {
		paddingVertical: 10,
		marginVertical: 5,
	},
	startText:{
		
	},
	messageContainer: {
		backgroundColor: '#1D2671',
		maxWidth:"90%",
		alignSelf: "flex-end",
		flexDirection: "row",
		borderRadius: 15,
		paddingHorizontal: 10,
		marginHorizontal: 10,
		paddingTop: 8,
		paddingBottom: 8,
		shadowColor:'grey',
		shadowOpacity:0.3,
		shadowOffset:{
			width:0,
			height:3
		},
		elevation:5,
	},
	startmessageContainer: {
		maxWidth:"90%",
		alignSelf: "flex-end",
		flexDirection: "row",
		borderRadius: 15,
		paddingHorizontal: 10,
		marginHorizontal: 10,
		paddingTop: 8,
		paddingBottom: 8,
		shadowColor:'grey',
		shadowOpacity:0.3,
		shadowOffset:{
			width:0,
			height:3
		},
		elevation:5,
	},

	timeView: {
		backgroundColor: "transparent",
		justifyContent: "flex-end",
		paddingLeft: 10,
	},

	time: {
		color: "lightgray",
		fontSize: 10,
	},

	iconContainer:{
		justifyContent:'center',
		alignItems:'center',
		fontSize:30,
	},
	rightIconButtonStyle: {
		justifyContent: "center",
		alignItems: "center",
		paddingRight: 20,
		paddingLeft: 5,
	},
    messageView: {
		backgroundColor: "transparent",
		maxWidth: "80%",
	},
	Textmessage: {
		color: "white",
		alignSelf: "flex-start",
		fontSize: 15,
	},
});

export default ExpandableListView;
