import React from "react";
import { View, Text, StyleSheet, TouchableOpacity, Image, FlatList } from "react-native";
import { useDispatch, useSelector } from "react-redux";
import { selectDialogMessages, setDialogMessages } from "../redux/infoSlice";
import { getNowTime } from "../utils/commonUtil";

const Options = ({ messageContent,  isOnLeft, messageType, sendJsonMessage, optionRefDec}) => {
    // redux
    const dispatch = useDispatch();
    const dialogMessages = useSelector(selectDialogMessages);
    

    const sendOption = (item) => {
        const [hour, min] = getNowTime();
		let prefix = "";
		switch (messageType) {
			case "option-START":
				prefix = "我在"
				break;
			case "option-END":
				prefix= "我要去"
				break
			case "option-ask":
				prefix="我的對面是"
				break
		}
        let message = item
        let inputTextJson = {
			'author':"user",
			'message':message,
			'time':hour + ':' + min,
			'type':'text'
		}
        dispatch(setDialogMessages(dialogMessages.concat(inputTextJson)))

        message = prefix + message
        inputTextJson = {
			'author':"user",
			'message':message,
			'time':hour + ':' + min,
			'type':'text'
		}
        sendJsonMessage(inputTextJson)
	}

    const renderItem = ({item,index}) =>{
        return (
            <TouchableOpacity onPress={()=>sendOption(item)} style={[styles.item]}>
                <View>
                    <View style={[styles.titleView]}>
                        {messageType!="option-options" ? <Image style={styles.tinyLogo} source={{uri:'https://appapi-tc.fg-retail.com.tw//Image/Upload/20210926/eee83edf-904a-471b-a421-4a37aa58cd52.jpg'}}/> : null}
                        <Text style={styles.title} >{item}</Text>
                    </View>
                    {optionRefDec==undefined ? null : <Text style={styles.title}>{optionRefDec[index]}</Text>}
                    
                </View>
            </TouchableOpacity>
        )
    }

    return (
        <View
            style={[
                styles.container,
                isOnLeft("messageContainer"),
            ]}
        >
            <FlatList
                style={styles.flatli}
                extraData={optionRefDec}
                data={messageContent}
                keyExtractor={item => item}
                renderItem={(item,index)=>renderItem(item,index)}
                horizontal={true}
            />
        </View>
    )

};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        width:"100%",
    },
    tinyLogo: {
        width: 20,
        height: 20,
        margin:"auto",
        marginVertical: 3,
        marginHorizontal: 3,
        overflow:"hidden",
    },
    flatli:{
        backgroundColor:'white',
    },
    item: {
        backgroundColor: '#48b8e8',
        padding: 10,
        borderRadius:15,
        marginVertical: 10,
        marginHorizontal: 6,
        display:"flex",
        flexDirection: "column",
    },
    title:{

        alignContent:"center",
        alignSelf:"center",
        justifyContent:"center",
        fontSize:20
    },
    titleView:{
        display:"flex",
        flexDirection: "row",
    }
});

export default Options;
