import { createSlice } from "@reduxjs/toolkit";

const UNKNOWNVALUE = -100;

export const mapSlice = createSlice({
    name: 'map',
    initialState: {
        mapFloor: UNKNOWNVALUE,
        floorArray: null,
        floorIndex: -1,
    },
    reducers:{
        setMapFloor: (state, action) => {
            console.log(`地圖的樓層設定為第 ${action.payload} 樓`);
            state.mapFloor = action.payload;
        },
        setFloorArray: (state, action) => {
            state.floorArray = action.payload;
            if (action.payload.length > 0) {
                console.log(`地圖的樓層指引依序為 ${action.payload}`);
            }
        },
        setFloorIndex: (state, action) => {
            state.floorIndex = action.payload;
        }
    },
});


// Selectors
export const selectMapFloor = (state) => state.map.mapFloor;
export const selectFloorArray = (state) => state.map.floorArray;
export const selectFloorIndex = (state) => state.map.floorIndex;

export const { setMapFloor, setFloorArray, setFloorIndex } = mapSlice.actions;

export default mapSlice.reducer; //nav(name) Reducer ==> navReducer