
const HTTP_SCHEME = 'https';
const DOMAIN_NAME = 'iasl.asia.edu.tw'; // 127.0.0.1
const PORT_WS = 808;
const PORT_django = 809;

/** get current time */
const getNowTime = () => {
    let today = new Date()
    let hour = today.getHours();
    let min = today.getMinutes();
    if (hour < 10) {
        hour = '0' + hour.toString()
    }
    if (min < 10) {
        min = '0' + min.toString()
    }
    return [hour, min];
};

/**  Construct svg path expression from instructions */
const changePath = (tmpInstructions) => {
    
    let path = "M"
    try {
        tmpInstructions.forEach((ins, index) => {

            if (index === 0) {
                path += ins[0] + "," + ins[1];
            }
            else {
                path+= "L" + ins[0]+ "," + ins[1];
            }
        });
    }
    catch(error) {
        throw error;
    }
    // console.log('svg path = ', path);
    return path
};

/**  Construct svg path's point array from instructions */
const getPathCoordinates = (tmpInstructions) => {
    let all_points = [];

    tmpInstructions.forEach((ins) => {
        all_points.push({x: ins[0], y: ins[1]});
    });

    return all_points;
};


// Basic Math
const radian2Degree = (radianValue) => {
    return radianValue * 180 / Math.PI;
};

const degree2Radian = (degreeValue) => {
    return degreeValue * Math.PI / 180;
}

const computeVector = (p1, p2) => {
    return {
        x: p2.x - p1.x, 
        y: p2.y - p1.y
    };
};

/** get -180~180 degree number. For canvas, 從x軸起算，順時針為正的角度 */
const calcAngleDegrees = (x, y) => {
    return radian2Degree(Math.atan2(y, x));
}

/** angleInDegree: 順時針的角度 */
const rotatePoint = (point, angleInDegree) => {
    const angleInRadian = degree2Radian(angleInDegree);

    return {
        x: point.x * Math.cos(angleInRadian) - point.y * Math.sin(angleInRadian), 
        y: point.x * Math.sin(angleInRadian) + point.y * Math.cos(angleInRadian)
    };
};

/** angleInDegree: 順時針的角度 */
const rotateRelativeToCenter = (arbitraryPoint, centerPoint, angleInDegree) => {
    const relativeCoordinate = {x: arbitraryPoint.x - centerPoint.x, y: arbitraryPoint.y - centerPoint.y};

    let rotatedVector = rotatePoint(relativeCoordinate, angleInDegree);
    return {
        x: centerPoint.x + rotatedVector.x, 
        y: centerPoint.y + rotatedVector.y
    };
};

export {
    DOMAIN_NAME,
    PORT_WS,
    PORT_django,
    HTTP_SCHEME,

    getNowTime,
    changePath, 
    getPathCoordinates, 

    radian2Degree, 
    degree2Radian,
    calcAngleDegrees,
    computeVector,
    rotatePoint,
    rotateRelativeToCenter,
};