import { View, Text, StyleSheet, TouchableOpacity } from "react-native";
import Icon from 'react-native-vector-icons/FontAwesome';
import { useSelector } from 'react-redux';
import { selectInstructionTexts } from '../redux/infoSlice';


const Start = ({isOnLeft, sendJsonMessage}) => {

	const instructiontext = useSelector(selectInstructionTexts);

    const sendStart = () => {
		if (Object.keys(instructiontext).length > 0) {
			// console.log('已經按過 START 了');
			return;
		}

		let inputTextJson = {
			'author':"user",
			'message':"<:START:>",
			'type':'text'
		}
		sendJsonMessage(inputTextJson);
	}

    return (
        <View style={[styles.container]}>
            <View
                style={[
                    styles.messageContainer,
                    isOnLeft("startmessageContainer"),
                ]}
            >
                <TouchableOpacity
                    style={styles.rightIconButtonStyle}
                    onPress={sendStart}
                >
                    <Text style={{color:"white"}}>
                        START  
                        <Icon name="play" size={15} color="white" />
                    </Text>
                    
                </TouchableOpacity>
            </View>
        </View>
    );
}

const styles = StyleSheet.create({
	container: {
		paddingVertical: 10,
		marginVertical: 5,
	},
    messageContainer: {
		backgroundColor: '#1D2671',
		maxWidth:"90%",
		alignSelf: "flex-end",
		flexDirection: "row",
		borderRadius: 15,
		paddingHorizontal: 10,
		marginHorizontal: 10,
		paddingTop: 8,
		paddingBottom: 8,
		shadowColor:'grey',
		shadowOpacity:0.3,
		shadowOffset:{
			width:0,
			height:3
		},
		elevation:5,
	},
    rightIconButtonStyle: {
		justifyContent: "center",
		alignItems: "center",
		paddingRight: 20,
		paddingLeft: 5,
	},
});
 
export default Start;