import React from "react";
import { View, Text, StyleSheet, Dimensions } from "react-native";

import MessageBox from "./Message.Normal";
import Animated, {
	useAnimatedStyle,
	useAnimatedGestureHandler,
	useSharedValue,
	withTiming,
	runOnJS,
} from "react-native-reanimated";
import {
	PanGestureHandler,
} from "react-native-gesture-handler";
import Icon from "react-native-vector-icons/FontAwesome";

import { useDispatch, useSelector } from "react-redux";
import { selectIndex } from '../redux/infoSlice';
import { setFloor, setIndex } from "../redux/infoSlice";
import { theme } from "../styling/theme";
import * as Speech from 'expo-speech';
import { selectFloorArray, selectFloorIndex, setFloorIndex, setMapFloor } from "../redux/mapSlice";

const InnerBox = ({ index,message,isOnLeft,floor, messageType,time,isLeft}) => {
	const dispatch = useDispatch();
	const floorArray = useSelector(selectFloorArray);
	const completeIndex = useSelector(selectIndex);

	const floorOrder = floorArray.indexOf(Number(floor));
	const completeFloorIndex = useSelector(selectFloorIndex);

    
	const AIcon = Animated.createAnimatedComponent(Icon)

    const x = useSharedValue(0);
    const uas = useAnimatedStyle(() => {
		return {
			transform: [{ translateX: x.value }]
		}
	});
    const SCREEN_WIDTH = Dimensions.get('window').width
	const TRANSLATE_X_THRESHOLD = - SCREEN_WIDTH *0.2

	const InstructionMsgeventHandler = useAnimatedGestureHandler({
		onActive: (event, ctx) => {
			if (isLeft){
				x.value = event.translationX;
			}
		},
		onEnd: (event, ctx) => {
			const shouldBeDismissed = x.value < TRANSLATE_X_THRESHOLD
			if (shouldBeDismissed){
				// runOnJS(Tts.speak)(message);
				runOnJS(Speech.speak)(message)
			}
			x.value = withTiming(0);
		}
	});

	const rIconStyle = useAnimatedStyle(()=>{
		const opacity = withTiming(
			x.value < TRANSLATE_X_THRESHOLD ? 1 : 1
		);
		return { opacity }
	})

    return (
        <View style={[styles.container]}>

            <PanGestureHandler
                onGestureEvent={InstructionMsgeventHandler}
                onHandlerStateChange={({ nativeEvent }) => {
                    if(nativeEvent.translationX > 40){
                        dispatch(setIndex(index));
                        dispatch(setFloor(floor));
						dispatch(setFloorIndex(floorOrder));
						dispatch(setMapFloor(floor));
                        console.log('complete floor index, index and floor : ', floorOrder, index, floor)
                    }
                }}
            >
				<Animated.View
					style={[
						styles.messageContainer,
						isOnLeft("messageContainer"),
						uas
					]}
				>
                            <MessageBox 
                                
                                messageContent={message}
                                isOnLeft={isOnLeft} 
                                messageType={messageType}
								isComplete={floorOrder < completeFloorIndex || (floorOrder == completeFloorIndex && index < completeIndex)}
							>

                            </MessageBox>
                            <View style={styles.timeView}>
                                <Text style={[styles.time, isOnLeft("time")]}>
                                    {time}
                                </Text>
                            </View>
							<Animated.View style={[styles.iconContainer, rIconStyle]}>
									<AIcon
										name="volume-up"
										color={theme.colors.description}
										style={{left:30}}
									/>
									<AIcon
										name="long-arrow-left"
										color={theme.colors.description}
										style={{left:30}}
									/>
							</Animated.View>
                        
                    </Animated.View>
            </PanGestureHandler>
         </View>

    )

};

const styles = StyleSheet.create({
	container: {
		paddingVertical: 10,
		marginVertical: 5,
	},
	startText:{
		
	},
	messageContainer: {
		backgroundColor: '#1D2671',
		maxWidth:"90%",
		alignSelf: "flex-end",
		flexDirection: "row",
		borderRadius: 15,
		paddingHorizontal: 10,
		marginHorizontal: 10,
		paddingTop: 8,
		paddingBottom: 8,
		shadowColor:'grey',
		shadowOpacity:0.3,
		shadowOffset:{
			width:0,
			height:3
		},
		elevation:5,
	},
	startmessageContainer: {
		maxWidth:"90%",
		alignSelf: "flex-end",
		flexDirection: "row",
		borderRadius: 15,
		paddingHorizontal: 10,
		marginHorizontal: 10,
		paddingTop: 8,
		paddingBottom: 8,
		shadowColor:'grey',
		shadowOpacity:0.3,
		shadowOffset:{
			width:0,
			height:3
		},
		elevation:5,
	},

	timeView: {
		backgroundColor: "transparent",
		justifyContent: "flex-end",
		paddingLeft: 10,
	},

	time: {
		color: "lightgray",
		fontSize: 10,
	},

	iconContainer:{
		// position:'absolute',
		// right:'40%',
		// height:30,
		// width:30,
		// top:'40%',
		justifyContent:'center',
		alignItems:'center',
		fontSize:30,
	},
	rightIconButtonStyle: {
		justifyContent: "center",
		alignItems: "center",
		paddingRight: 20,
		paddingLeft: 5,
	},
    messageView: {
		backgroundColor: "transparent",
		maxWidth: "80%",
	},
	Textmessage: {
		color: "white",
		alignSelf: "flex-start",
		fontSize: 15,
	},
});

export default InnerBox;
