import React from 'react';
import { View, Text, Image, StyleSheet} from 'react-native';
import { theme } from '../styling/theme';


const OnlineStatus = ({ onlineStatus }) => {
    const onlineText = 'Online';
    const onlineUri = 'https://miro.medium.com/max/512/1*nZ9VwHTLxAfNCuCjYAkajg.png';
    const OfflineText = 'Offline'
    const offlineUri = 'https://pngimage.net/wp-content/uploads/2018/06/rec-icon-png-5.png';
    return onlineStatus ? (
        <View style={styles.onlineStatusContainer}>
            <Text style={styles.onlineStatus}>{onlineText}</Text>
            <Image style={styles.statusImage} source={{uri:onlineUri}} />
        </View>
        )
        :(
        <View style={styles.onlineStatusContainer}>
            <Text style={styles.onlineStatus}>{OfflineText}</Text>
            <Image style={styles.statusImage} source={{uri:offlineUri}} />
        </View>
        )
}

const styles = StyleSheet.create({
    onlineStatusContainer:{
		flexDirection:'row',
        paddingHorizontal: 20
	},
    onlineStatus: {
        color: theme.colors.white,
        fontSize: 16,
    },
    statusImage:{
        height: 5,
        width: 5,
        borderRadius: 32.5,
        alignSelf:'center',
        margin:3,
    }
})
export default OnlineStatus;