import { useState, useEffect } from 'react';
import { StyleSheet, View, StatusBar, Platform, SafeAreaView, Dimensions  } from 'react-native';
import { useDispatch, useSelector } from 'react-redux';
import { selectDialogMessages, selectInstructions, selectInstructionTexts, setDialogMessages, setInstructions, setInstructionTexts } from '../redux/infoSlice';
import useWebSocket, { ReadyState } from 'react-native-use-websocket';
import ChatHeader from '../components/ChatBot.Header';
import ChatImageMap from '../components/ChatBot.ImageMap';
import ChatInput from '../components/ChatBot.InputBox';
import ChatMessagessList from '../components/ChatBot.MessageList';
import { DOMAIN_NAME, PORT_WS } from '../utils/commonUtil';

// websocket url: (local)  ws://10.0.2.2:8000/ws/bots/       ws://127.0.0.1:8000/ws/bots/
// websocket url: (remote) ws://61.219.68.130:808/websocket

const window = Dimensions.get("window");
const screen = Dimensions.get("screen");

const ChatBotScreen = ({navigation}) => {
    const [botname, setBotName] = useState("Indoor Navigation");

    // use ws: if you test on android Emulator
    let ws_scheme = 'wss:';
    const [socketUrl, setSocketUrl] = useState(`${ws_scheme}//${DOMAIN_NAME}:${PORT_WS}/websocket`);
    const [onlinestatus, setOnlineStatus] = useState(false);

    // redux
    const dispatch =  useDispatch();
    const instructions = useSelector(selectInstructions);
    const instructiontext = useSelector(selectInstructionTexts);
    const dialogMessages = useSelector(selectDialogMessages);

    // websocket
    const { sendJsonMessage, lastJsonMessage, readyState } = useWebSocket(socketUrl, {
        onOpen: () => {
            console.log('connected');
            setOnlineStatus(true);
        },
        onError: (err) => {
            console.log("[CHATBOTIASL][ERROR][WEBSOCKET]:", err);
        },
        shouldReconnect: (closeEvent) => true,
        reconnectAttempts: 30,
        reconnectInterval: 3000,
    });
      
    const connectionStatus = {
        [ReadyState.CONNECTING]: "Connecting",
        [ReadyState.OPEN]: "Open",
        [ReadyState.CLOSING]: "Closing",
        [ReadyState.CLOSED]: "Closed",
        [ReadyState.UNINSTANTIATED]: "Uninstantiated",
    }[readyState];

    const [dimensions, setDimensions] = useState({ window, screen });

    //connectionStatus change UI
    useEffect(()=> {
        setOnlineStatus(connectionStatus === 'Open');
    }, [connectionStatus]);
    
    //onmessage
    useEffect(() => {
        if (Platform.OS != 'web') {
            console.log('message : ',lastJsonMessage);
        }

        if (lastJsonMessage !== null && lastJsonMessage !== {}) {
            if (lastJsonMessage.instructions !== undefined) {
                try {
                    let newDict = {...instructions, ...lastJsonMessage.instructions};
                    dispatch(setInstructions(newDict));
                }
                catch{}

                if (lastJsonMessage.type === 'instructiontext') {
                    let newDict2 = JSON.parse(JSON.stringify(instructiontext));
                    newDict2[lastJsonMessage.floor] = lastJsonMessage.message[0];
                    dispatch(setInstructionTexts(newDict2));
                }
            }

            dispatch(setDialogMessages(dialogMessages.concat(lastJsonMessage)));
        }
    }, [lastJsonMessage]);

    useEffect(() => {
        console.log('Your platform :', Platform.OS);
        
        const subscription = Dimensions.addEventListener("change", ({ window, screen }) => {
            setDimensions({ window, screen });
        });

        return () => subscription?.remove();
    }, []);

    if (Platform.OS === 'web') {
        return (
            <SafeAreaView style={{height: dimensions.window.height-5}}>
                <ChatHeader
                    botname={botname}
                    locationName={"遠雄廣場"}
                    onlineStatus={onlinestatus} 
                    sendJsonMessage={sendJsonMessage}
                />
                <ChatImageMap />
                <ChatMessagessList
                    sendJsonMessage={sendJsonMessage}
                />
                <ChatInput
                    online={onlinestatus}
                    sendJsonMessage={sendJsonMessage}
                />
            </SafeAreaView>
        );
    }
    else {
        return (
            <SafeAreaView style={styles.crossPlatformContainer}>
                <StatusBar backgroundColor="#b3e6ff" barStyle="dark-content" />
                <ChatHeader
                    botname={botname}
                    locationName={"遠雄廣場"}
                    onlineStatus={onlinestatus} 
                    sendJsonMessage={sendJsonMessage}
                />
                <ChatImageMap />
                <ChatMessagessList
                    sendJsonMessage={sendJsonMessage}
                />
                <ChatInput
                    online={onlinestatus}
                    sendJsonMessage={sendJsonMessage}
                />
            </SafeAreaView>
        );
    }
}

const styles = StyleSheet.create({
    crossPlatformContainer: {
        flex: 1,
        paddingTop: Platform.OS === 'android' ? 0 : 0
    },
});

export default ChatBotScreen;
