import React from "react";
import { View, Text, StyleSheet, Dimensions, TouchableOpacity } from "react-native";
import {
	PanGestureHandler,
} from "react-native-gesture-handler";
import Animated, {
	useAnimatedStyle,
	useAnimatedGestureHandler,
	useSharedValue,
	withTiming,
	runOnJS,
} from "react-native-reanimated";
import { theme } from "../styling/theme";

import MessageBox from "./Message.Normal";
import Icon from 'react-native-vector-icons/FontAwesome';
import ExpandableListV from "./Message.Expandable";
import Options from "./Message.Options"
import * as Speech from 'expo-speech';
import Start from "./Message.Start";


const Message = ({ time, isLeft, message, messageType, floor, index,optionRefDec, setScrollState, sendJsonMessage}) => {
	const x = useSharedValue(0);
	const largerscale = useSharedValue(12);


	const AIcon = Animated.createAnimatedComponent(Icon)

	const SCREEN_WIDTH = Dimensions.get('window').width;
	const TRANSLATE_X_THRESHOLD = - SCREEN_WIDTH *0.2;

	const isOnLeft = (type) => {
		if (isLeft && type === "messageContainer") {
			return {
				alignSelf: "flex-start",
				backgroundColor: "#f0f0f0",
				borderTopLeftRadius: 0,
			}
		}
		else if (isLeft && type === "startmessageContainer"){
			return {
				alignSelf: "flex-start",
				backgroundColor: "#1B17E6",
				borderTopLeftRadius: 0,
			};
		
		}
		else if ((isLeft && type === "text") || (isLeft && type ==="instructiontext")) {
			return {
				color: "#000",
			};
		}
		else if (isLeft && type === "time") {
			return {
				color: "darkgray",
			};
		}
		else {
			return {
				borderTopRightRadius: 0,
			};
		}
	};
	

	const NoralMsgeventHandler = useAnimatedGestureHandler({
		onActive: (event, ctx) => {
			if (isLeft && event.translationX<0){
				x.value = event.translationX;
				largerscale.value =  event.translationX
			}
		},
		onEnd: (event, ctx) => {
			const shouldBeDismissed = x.value < TRANSLATE_X_THRESHOLD
			if (shouldBeDismissed){
				// runOnJS(Tts.speak)(message);
				runOnJS(Speech.speak)(message)
			}
			x.value = withTiming(0);
			largerscale.value = withTiming(0)
		}
	});



	const rIconStyle = useAnimatedStyle(()=>{
		const opacity = withTiming(
			x.value < TRANSLATE_X_THRESHOLD ? 1 : 1
		);
		return { opacity }
	})



	const uas = useAnimatedStyle(() => {
		return {
			transform: [{ translateX: x.value }]
		}
	});

	const enlarge = useAnimatedStyle(() => {
		const fontSize = withTiming(
			largerscale.value < TRANSLATE_X_THRESHOLD ? 20 : 12
		);
		return { fontSize:fontSize }
	});

	switch(messageType){
		case 'state':
			return (
				<Start 
					isOnLeft={isOnLeft}
					sendJsonMessage={sendJsonMessage}
				/>
			)

		case 'option-START':
			return (
				<Options
					messageContent={message}
					isOnLeft={isOnLeft}
					messageType={messageType}
					sendJsonMessage={sendJsonMessage}
					optionRefDec={optionRefDec}
				/>
			)
		case 'option-END':
			return (
				<Options
					messageContent={message}
					isOnLeft={isOnLeft}
					messageType={messageType}
					sendJsonMessage={sendJsonMessage}
					optionRefDec={optionRefDec}
				/>
			)
		case 'option-ASK':
			return (
				<Options
					messageContent={message}
					isOnLeft={isOnLeft}
					messageType={messageType}
					sendJsonMessage={sendJsonMessage}
					optionRefDec={optionRefDec}
				/>
			)
		
		case 'option-options':
			return (
				<Options
					messageContent={message}
					isOnLeft={isOnLeft}
					messageType={messageType}
					sendJsonMessage={sendJsonMessage}
				/>
			)
		case 'instructiontext':
			return (
				<ExpandableListV
					messageContent={message}
					isOnLeft={isOnLeft}
					messageType={messageType}
					time={time}
					isLeft={isLeft}
					floor={floor}
					// Tts={Tts}
				/>
			)
		case 'text':
			return (
					<View style={[styles.container]}>

						<PanGestureHandler
							onGestureEvent={NoralMsgeventHandler}
						>
		
							<Animated.View style={uas}>
								<View
									style={[
										styles.messageContainer,
										isOnLeft("messageContainer"),
									]}
								>
		
									<MessageBox
										isOnLeft={isOnLeft}
										messageType={messageType}
										messageContent={message}
										isComplete={false}
									/>
									<View style={styles.timeView}>
										<Text style={[styles.time, isOnLeft("time")]}>
											{time}
										</Text>
									</View>
									<Animated.View style={[styles.iconContainer, rIconStyle]}>
										<AIcon
											name="volume-up"
											color={theme.colors.description}
											style={[{left:30},enlarge]}
										/>
										<AIcon
											name="long-arrow-left"
											color={theme.colors.description}
											style={[{left:30},enlarge]}
										/>
									</Animated.View>
		
								</View>
							</Animated.View>
						</PanGestureHandler>

					</View>
			)
		case 'image':
			return (
				// empty view, this has been moved to ChatBot.ImageMap.jsx
				<View></View>
			);

			

		// 	return (
		// 		<Animated.View style={[styles.container]}>
		// 			<View
		// 				style={[
		// 					styles.messageContainer,
		// 					isOnLeft("messageContainer"),
		// 				]}
		// 			>
		// 				<ImageBox
		// 					isOnLeft={isOnLeft}
		// 					messageContent={message}
		// 					messageType={messageType}
		// 					floor={floor}
		// 					setScrollState={setScrollState}
		// 				/>
		// 				<View style={styles.timeView}>
		// 					<Text style={[styles.time, isOnLeft("time")]}>
		// 						{time}
		// 					</Text>
		// 				</View>
		// 			</View>
		// 		</Animated.View>
		// 	)
	}

};

const styles = StyleSheet.create({
	container: {
		paddingVertical: 10,
		marginVertical: 5,
	},
	messageContainer: {
		backgroundColor: '#1D2671',
		maxWidth:"90%",
		alignSelf: "flex-end",
		flexDirection: "row",
		borderRadius: 15,
		paddingHorizontal: 10,
		marginHorizontal: 10,
		paddingTop: 8,
		paddingBottom: 8,
		shadowColor:'grey',
		shadowOpacity:0.3,
		shadowOffset:{
			width:0,
			height:3
		},
		elevation:5,
	},
	timeView: {
		backgroundColor: "transparent",
		justifyContent: "flex-end",
		paddingLeft: 10,
	},
	time: {
		color: "lightgray",
		fontSize: 10,
	},
	iconContainer:{
		justifyContent:'center',
		alignItems:'center',
		fontSize:30,
	},
});

export default Message;
