import React, { useState, useEffect, useRef } from "react";
import { View,StyleSheet,TextInput, Platform, TouchableOpacity,KeyboardAvoidingView } from "react-native";
import Modal from "react-native-modal";

// import Voice from "@react-native-voice/voice";

import Icon from "react-native-vector-icons/FontAwesome";
import { theme } from "../styling/theme";

import { useDispatch, useSelector } from "react-redux";
import { setFloorArray, setMapFloor } from "../redux/mapSlice";
import { selectDialogMessages, setDialogMessages, setInstructionTexts } from "../redux/infoSlice";
import { getNowTime } from "../utils/commonUtil";
import ModalWebSpeech from "./Modal.WebSpeech";

const ChatInput = ({ online, sendJsonMessage}) => {
	const dispatch = useDispatch();
	const dialogMessages = useSelector(selectDialogMessages);


	const [input, setInput] = useState("");
	const [microphonecolor, setMicrophoneColor] = useState(theme.colors.description);
	const [isWebRecording, setIsWebRecording] = useState(false);
	const [webSpeechMiddleResult, setWebSpeechMiddleResult] = useState('');
	
	let recognitionWebRef = useRef(undefined);
	let cacheInputTextRef = useRef('');

	useEffect(() => {
		if (Platform.OS === 'web') {
			if ('SpeechRecognition' in window || 'webkitSpeechRecognition' in window) {
				console.log("speech recognition API supported");

				recognitionWebRef.current = new window.webkitSpeechRecognition();
				recognitionWebRef.current.continuous = true;
				recognitionWebRef.current.interimResults = true;
				recognitionWebRef.current.lang = 'cmn-Hant-TW'; // 中文 (台灣)

				// register related events
				recognitionWebRef.current.onstart = () => { // 開始辨識
					// console.log('webkitSpeechRecognition event: onstart');
					setIsWebRecording(true); // 設定為辨識中
					setMicrophoneColor(theme.colors.danger);
					
					cacheInputTextRef.current = input;
				};
				recognitionWebRef.current.onend = () => { // 辨識完成
					// console.log('webkitSpeechRecognition event: onend');
					setIsWebRecording(false); // 設定為「非辨識中」
					setMicrophoneColor(theme.colors.description);
				};
				recognitionWebRef.current.onresult = (event) => { // 辨識有任何結果時
					// console.log('event.results = ', event.results);
					var interim_transcript = '';
					var final_transcript = '';
	
					for (let resultList of event.results) {
						// [0]: SpeechRecognitionAlternative {transcript: '活動中心地下室', confidence: 0.009999999776482582}
						if (resultList[0].confidence < 0.000001) {
							continue; // 不取confidence接近0的結果
						}
	
						if (resultList.isFinal) {
							final_transcript += resultList[0].transcript;
						}
						else {
							interim_transcript += resultList[0].transcript;
						}
					}

					// this.interimTranscriptSubject.next(interim_transcript);
					// this.finalTranscriptSubject.next(final_transcript);
					setWebSpeechMiddleResult(interim_transcript);
					setInput(cacheInputTextRef.current + final_transcript);
				};
				recognitionWebRef.current.onerror = (event) => {
					if (event.error === 'not-allowed') {
						// Permission to use microphone is blocked.
						console.log('webkitSpeechRecognition event: onerror');
						console.log(event);
					}
				};
			}
			else {
				console.log("speech recognition API not supported");
			}
		}
		else {
			// Voice.getSpeechRecognitionServices();
			// Voice.onSpeechStart = onSpeechStartHandler;
			// Voice.onSpeechEnd = onSpeechEndHandler;
			// Voice.onSpeechResults = onSpeechResultsHandler;
			// Voice.onSpeechError = onSpeechErrorHandler;

			// return () => {
			// 	Voice.destroy().then(Voice.removeAllListeners);
			// }
		}
	}, [])

	// const onSpeechErrorHandler = async (e) => {
	// 	console.log("error ==>", e)
	// 	await destroyVoiceInstance()
	// }

	// const destroyVoiceInstance = async () => {
	// 	try{
	// 		await Voice.stop();
	// 		await Voice.destroy().then(Voice.removeAllListeners());
	// 	}
	// 	catch(e){
	// 		console.log(e)
	// 	}
		
	// }
	
	// const onSpeechStartHandler = async (e) => {
	// 	console.log("start handler==>>>", e)
	// }
	// const onSpeechEndHandler = async (e) => {
	// 	console.log("stop handler", e)
	// 	try {
	// 		await Voice.stop()
	// 		setMicrophoneColor(theme.colors.description)
	// 	} catch (error) {
	// 		console.log("error raised", error)
	// 	}
	// }
	
	// const onSpeechResultsHandler = async (e) => {
	// 	let text = e.value[0]
	// 	let result = input + text
	// 	setInput(result)
	// 	console.log("speech result handler", e)
	// 	try {
	// 		await Voice.stop()
	// 		setMicrophoneColor(theme.colors.description)
	// 	} catch (error) {
	// 		console.log("error raised", error)
	// 	}
	// }
	
	const startRecording = async () => {
		if (Platform.OS === 'web') {
			recognitionWebRef.current.start();
		}
		// else {
		// 	try {
		// 		setMicrophoneColor(theme.colors.danger)
		// 		await Voice.start('zh-TW', {
		// 			"RECOGNIZER_ENGINE": "GOOGLE",
		// 			 "EXTRA_PARTIAL_RESULTS": true
		// 		  })
		// 		console.log('started')
		// 	} 
		// 	catch (error) {
		// 		console.log("error raised", error)
		// 	}
		// }
	}
	const stopRecording = async () => {
		if (Platform.OS === 'web') {
			recognitionWebRef.current.stop();
		}
		else {
			// try {
			// 	await Voice.stop()
			// 	setMicrophoneColor(theme.colors.description)
			// } 
			// catch (error) {
			// 	console.log("error raised", error)
			// }
		}
	}

	const handleSubmit = () => {
		if (input === '') {
			return;
		}

		let [hour, min] = getNowTime();

		let inputTextJson = {
			'author': "user",
			'type':'text',
			'message': input,
			'time': hour + ':' + min,
		}
		sendJsonMessage(inputTextJson);

		if (input !== "/done") {
			dispatch(setDialogMessages(dialogMessages.concat(inputTextJson)));
		}
		else {
			dispatch(setDialogMessages([]));
			dispatch(setMapFloor(-100));
			dispatch(setInstructionTexts({}));
			dispatch(setFloorArray([]));
		}

		setInput('');
	}


	return (
		<KeyboardAvoidingView behavior={Platform.OS === "ios" ? "padding" : "height"} style={styles.container}>

			<View style={styles.innerContainer}>
				<View style={styles.inputAndMicrophone}>
					<TextInput
						multiline
						placeholder={"Type something..."}
						style={styles.input}
						value={input}
						onChangeText={(text) => setInput(text)}
					/>
					<TouchableOpacity
						style={styles.rightIconButtonStyle}
						onPress={startRecording}
					>
					<Icon
						name="microphone"
						size={23}
						color={microphonecolor}
					/>
					</TouchableOpacity>

				</View>
				<TouchableOpacity
					style={styles.sendButton} 
					disabled={!online} 
					onPress={handleSubmit}
				>
					<Icon
						name="send"
						size={23}
						color={theme.colors.white}
					/>
				</TouchableOpacity>


				{/** Modal implementation */}
				<Modal isVisible={isWebRecording} useNativeDriver={true} backdropOpacity={0.5}>
					<ModalWebSpeech webSpeechMiddleResult={webSpeechMiddleResult} stopRecording={stopRecording} />
				</Modal>
			</View>
      		
	  	</KeyboardAvoidingView>
	);
};

const styles = StyleSheet.create({
	container: {
		justifyContent: "center",
		backgroundColor: theme.colors.white,
		position:"relative",
	},
	innerContainer: {
		paddingHorizontal: 10,
		marginHorizontal: 13,
		bottom: 2,
		justifyContent: "space-between",
		alignItems: "center",
		flexDirection: "row",
		paddingVertical: 1,
		height: 70,
	},
	inputAndMicrophone: {
		flexDirection: "row",
		backgroundColor: theme.colors.inputBackground,
		flex: 3,
		marginRight: 10,
		paddingVertical: Platform.OS === "ios" ? 10 : 0,
		borderRadius: 30,
		alignItems: "center",
		justifyContent: "space-between",
	},
	input: {
		backgroundColor: "transparent",
		paddingLeft: 20,
		margin: 3,
		color: theme.colors.inputText,
		flex: 2,
		fontSize: 15,
		height: 35,
		alignSelf: "center",
		justifyContent:'center',
	},
	rightIconButtonStyle: {
		justifyContent: "center",
		alignItems: "center",
		paddingRight: 20,
		paddingLeft: 10,
		borderLeftWidth: 1,
		borderLeftColor: "#fff",
	},
	swipeToCancelView: {
		flexDirection: "row",
		alignItems: "center",
		marginRight: 30,
	},
	swipeText: {
		color: theme.colors.description,
		fontSize: 15,
	},
	emoticonButton: {
		justifyContent: "center",
		alignItems: "center",
		paddingLeft: 10,
	},
	recordingActive: {
		flexDirection: "row",
		alignItems: "center",
		justifyContent: "space-between",
		paddingLeft: 10,
	},
	recordingTime: {
		color: theme.colors.description,
		fontSize: 20,
		marginLeft: 5,
	},
	microphoneAndLock: {
		alignItems: "center",
		justifyContent: "flex-end",
	},
	lockView: {
		backgroundColor: "#eee",
		width: 60,
		alignItems: "center",
		borderTopLeftRadius: 30,
		borderTopRightRadius: 30,
		height: 130,
		paddingTop: 20,
	},
	sendButton: {
		backgroundColor: theme.colors.primary,
		borderRadius: 40,
		height: 40,
		width: 40,
		alignItems: "center",
		justifyContent: "center",
		paddingRight: 2,
	},
});

export default ChatInput;