import React, { useState, useEffect } from "react";

import { View, StyleSheet, ImageBackground} from 'react-native';
import ReactNativeZoomableView from '@openspacelabs/react-native-zoomable-view/src/ReactNativeZoomableView';
import Svg ,{ Defs, Marker, Path, Circle } from 'react-native-svg';


import { useSelector } from 'react-redux';
import { selectIndex, selectInstructions } from '../redux/infoSlice';
import { selectMapFloor } from "../redux/mapSlice";
import { changePath, DOMAIN_NAME, HTTP_SCHEME, PORT_django } from "../utils/commonUtil";


const ChatImageMap = () => {
    // https://reactjs.org/logo-og.png
    // http://61.219.68.130:809/static/image/03.jpeg
    const [imageURL, setImageURL] = useState(`${HTTP_SCHEME}://${DOMAIN_NAME}:${PORT_django}/static/image/03.jpeg`)

    // svg-related settings
    var svg_width = 409.6;
    var svg_height = 222.8;
    var viewBoxProps = "0 0 1024 557"; // equal to original image's size
    const [svgPath, setSvgPath] = useState('M 0 0 L 0 0');
    
    // redux
    const instructions = useSelector(selectInstructions);
    const mapFloor = useSelector(selectMapFloor);
    const index = useSelector(selectIndex);

    useEffect(() => {
        if (mapFloor > -100 && index >= 0) {
            let tmpInstructions = instructions[mapFloor.toString()];
            if (index > 0) {
                tmpInstructions = tmpInstructions.slice(index);
            }
            setSvgPath(changePath(tmpInstructions));
            setImageURL(`${HTTP_SCHEME}://${DOMAIN_NAME}:${PORT_django}/static/image/0${mapFloor}.jpeg`);
        }
        else {
            // default image before indoor guide
            setSvgPath('M 0 0 L 0 0');
            setImageURL(`${HTTP_SCHEME}://${DOMAIN_NAME}:${PORT_django}/static/image/iFG-cover.jpg`);
        }
    }, [mapFloor, index]);

    

    return (
        <View style={styles.container}>
            <ReactNativeZoomableView
                maxZoom={3}
                minZoom={1}
                zoomStep={0.5}
                initialZoom={1}
                bindToBorders={true}
                style={styles.messageView}
            >
                <ImageBackground source={{ uri: imageURL }} style={[styles.Imagemessage, {width: svg_width, height: svg_height}]}>
                    <Svg width={svg_width} height={svg_height} viewBox={viewBoxProps}>
                        <Defs>
                            <Marker
                                id="Triangle"
                                viewBox="0 0 10 10"
                                refX="0"
                                refY="5"
                                markerUnits="strokeWidth"
                                markerWidth="3"
                                markerHeight="3"
                                orient="auto"
                            >
                                <Path
                                    d="M 0 0 L 10 5 L 0 10 z" 
                                    fill="black"
                                    stroke="red"
                                    strokeWidth="1"
                                />
                            </Marker>
                            <Marker
                                id="startPoint"
                                viewBox="0 0 10 10"
                                refX="5"
                                refY="5"
                                markerUnits="strokeWidth"
                                markerWidth="3"
                                markerHeight="3"
                                orient="auto"
                            >
                                    <Circle 
                                        cx="5" 
                                        cy="5" 
                                        r="5" 
                                        fill="green"
                                    />
                            </Marker>
                            <Marker
                                id="midPoint"
                                viewBox="0 0 10 10"
                                refX="5"
                                refY="5"
                                markerUnits="strokeWidth"
                                markerWidth="2"
                                markerHeight="2"
                                orient="auto"
                            >
                                <Circle cx="5" cy="5" r="5" fill="grey"/>
                            </Marker>
                        </Defs>

                        <Path
                            d={svgPath}
                            // d="M 0 0 L 10 5 L 0 10 z"
                            fill="none"
                            stroke="black"
                            strokeWidth="10"
                            markerStart="url(#startPoint)"
                            markerMid="url(#midPoint)"
                            markerEnd="url(#Triangle)"
                        />
                    </Svg>
                </ImageBackground>
            </ReactNativeZoomableView>
        </View>
    );
}


const styles = StyleSheet.create({
    container: {
        borderColor: "#9879e0",
        borderBottomWidth: 1.5,
    },
    messageView: {
		backgroundColor: "transparent",
		maxWidth: "100%",
	},
    Imagemessage: {
		alignSelf: "center",
	},
})

export default ChatImageMap;