import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    instructions:{},
    instructionTexts: {}, // Key: floor number; value: instruction text object
    floor:-10,
    index:-1,
    reply:null,
    message:null,
    dialogMessages: [],
}

export const infoSlice = createSlice({
    name:'info',
    initialState,
    reducers:{
        setInstructions:(state, action)=>{
            state.instructions = action.payload;
        },
        setInstructionTexts: (state, action) => {
            state.instructionTexts = action.payload;
        },
        setIndex:(state, action)=>{
            state.index = action.payload;
        },
        setReply:(state, action)=>{
            state.reply = action.payload;
        },
        setMessage:(state, action)=>{
            state.message = action.payload;
        },
        setFloor:(state, action)=>{
            state.floor = action.payload;
        },
        setDialogMessages: (state, action) => {
            state.dialogMessages = action.payload;
        }
    },
});

export const { setInstructions, setInstructionTexts, setIndex, setReply, setMessage, setFloor, setDialogMessages } = 
    infoSlice.actions;

// Selectors
export const selectInstructions = (state) => state.info.instructions;
export const selectInstructionTexts = (state) => state.info.instructionTexts;
export const selectIndex = (state) => state.info.index;
export const selectReply = (state) => state.info.reply;
export const selectMessage = (state) => state.info.message;
export const selectFloor = (state) => state.info.floor;
export const selectDialogMessages = (state) => state.info.dialogMessages;


export default infoSlice.reducer; //nav(name) Reducer ==> navReducer