import React from 'react';
import { View, Text, StyleSheet} from 'react-native';


const MessageBox = ({ messageContent, isOnLeft, messageType, isComplete}) => {

	let finishTextColorStyle = {
		color: "#d1d2de",
	};


    return (
        <View style={styles.messageView}>
            <Text style={[styles.Textmessage, isOnLeft(messageType), isComplete? finishTextColorStyle: {}]}>
                {messageContent}
            </Text>
        </View>
        
    );

};

const styles = StyleSheet.create({
	messageView: {
		backgroundColor: "transparent",
		maxWidth: "80%",
	},
	Textmessage: {
		color: "white",
		alignSelf: "flex-start",
		fontSize: 15,
	},
})

export default MessageBox;